import React, { useCallback, useState } from 'react';
import Header from '../components/Header';
import NewsSection from '../components/NewsSection.js';
import Footer from '../components/Footer';
import Category from '../components/Category';


const News = () => {
    const[category, setCatgory] = useState('all');
    const onSelect = useCallback(category=>setCatgory(category), [])


    return (
        <div className="wrap">
         <Header/>
         <Category category={category} onSelect={onSelect}/>
         <NewsSection category={category}/>
         <Footer/>  
        </div>
    );
};

export default News;