import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import axios from 'axios';
import NewsItem from './NewsItem';
import {AiOutlineLoading3Quarters} from 'react-icons/ai';



const NewsSectionBlock = styled.div`
  
  .row {padding-top:80px ; display:flex; flex-wrap:wrap;}
  .loadWrap {
    font-size: 200px;
    min-height: 800px;
    text-align: center;
    line-height: 800px;
    color: #000;
    .loadiocn{animation: loadpin infinite 5s linear;}
    @keyframes loadpin {
        from{transform: rotate(0deg);}
        to{transform: rotate(360deg);}
    }
}

 
`;


const NewsSection = ({category}) => {
    const[articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);

    const getNews = async () => {
        const query = category === 'all' ? '': `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=d1d090261a084402bfd9624fb3ef9c16`);
       // console.log(response)
        setArticles(response.data.articles);
        setLoading(true);
    }

    useEffect (
        ()=> {
            getNews()
        },[category]
    )

    if(!loading){
        return <NewsSectionBlock>
      <div className="loadWrap">
        <AiOutlineLoading3Quarters className='loadiocn' />
      </div>
      </NewsSectionBlock>
    }else {
        if (!articles){
            return <NewsSectionBlock></NewsSectionBlock>;
        }else{
            return (
                <NewsSectionBlock>
                    <div className="row"> 
                    {
                              articles.map((article) => (<NewsItem key={article.url} article={article}/>) )
                    }
                    </div>
                </NewsSectionBlock>
            );
        }
    }

};

export default NewsSection;