import React, { useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';

const NewsDetailBlock = styled.div`
     border-top:80px solid #ffb6c1;
     .row {padding-top:80px ; text-align: center};
     p:nth-child(1){font-size:30px; padding-bottom:50px;font-weight:bold};
     p:nth-child(2){
          img {width:50%}
          };
     p:nth-child(3) {margin:10px 15%; text-align:left; }

`


const NewsDetail = (props) => {
    // console.log(props)
    const {location, history} = props;
    useEffect (
        () => {
            if(location.state === undefined){
                history.push('/')
            }
        }, []
    )
    if (location.state){
        return (
            <div className="wrap">
                <Header/>
                <NewsDetailBlock>
                    <div className="row">
                        <p>{location.state.title}</p>
                        <p><img src={location.state.urlToImage} alt={location.state.title}/></p>
                        <p>{location.state.description}</p>
                    </div>
                </NewsDetailBlock>
                <Footer/>
            </div>
        );
    }else{
        return null;
    }
   
};

export default NewsDetail;