import React from 'react';
import styled from 'styled-components';

const ProductItmeBlock = styled.div`
width:50%;
display:flex;
margin-bottom:30px;
img{ width:130px}
div{ padding:0 10px;
p:nth-child(1){font-size:20px};
p:nth-child(2){font-size:18px; margin:10px 0px;};
}
`;

const ProductItme = ({name, depart, about, photo}) => {
    return (
        <ProductItmeBlock>
            <img src={photo} alt={name}/>
            <div>
                <p>{name}</p>
                <p>{depart}</p>
                <p>{about}</p>
            </div>
        </ProductItmeBlock>
    );
};

export default ProductItme;